import React, { useState, useEffect, useRef } from "react";
import Layout from "./Layout";
import { contentsHandler } from "../util/handler";

const styleSheet = {
  container: {
    background: "black",
    height: "1000px",
    color: "white",
    textAlign: "center",
    paddingTop: "30px",
    fontSize: "4em"
  }
};
const View = ({ data }) => {
  const inputRef = useRef();
  const [lyrics, setLyrics] = useState(contentsHandler(data));
  const [list, setList] = useState("");
  const dataHandler = () => {
    let main = 0;
    let sub = 0;
    setList(lyrics[main].contents[sub]);
  };
  useEffect(() => {
    dataHandler();
    console.log("HHH");
  }, [inputRef]);

  return (
    <Layout title="View">
      <input type="hidden" id="data" ref={useRef} />
      <div style={styleSheet.container}>
        {list.split("\n").map((it, i) => {
          return (
            <span key={i}>
              {it}
              <br />
            </span>
          );
        })}
      </div>
    </Layout>
  );
};

export default View;
